te3d(0, 100%, 0);\n    transform: translate3d(0, 100%, 0);\n    opacity: 1;\n  }\n\n  to {\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@font-face {\n  font-family: \"frameIcon\";\n  src: url(\"../assets/images/ic-player-frame-jump.ttf?rxg7gj\") format('truetype');\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'op-icons';\n  src: url('../assets/fonts/fontello.ttf?13010392') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'op-seek-icons';\n  src: url('../assets/fonts/seek-icons.ttf?13010392') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n}\n\n/**\n * @brief   common style\n * */\n\n:root {\n  --op-accent-color: #50e3c2;\n}\n\n.op-wrapper.ovenplayer {\n  position: relative;\n  max-height: 100%;\n\n  overflow: hidden;\n  zoom: 1 !important;\n  width: 100%;\n  display: block;\n  font-family: Helvetica, Arial, sans-serif;\n  background-color: #000;\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box;\n  color: #fff;\n  font-size: 14px;\n  font-weight: 100;\n  outline: 0;\n\n  -webkit-touch-callout: none;\n  user-select: none;\n\n  * {\n    box-sizing: inherit;\n  }\n\n  object {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n  }\n\n  &:before,\n  &:after {\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n  }\n\n  & *,\n  & *:before,\n  & *:after {\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n  }\n\n  &.op-fullscreen {\n    width: 100% !important;\n    height: 100% !important;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: 9999;\n    margin: 0;\n    position: fixed;\n\n    .op-watermark-container,\n    .op-thumbnail-container {\n      width: 100vw;\n      max-width: calc(100vh * 16 / 9);\n      height: calc(100vw * 9 / 16);\n      max-height: 100vh;\n      top: 50%;\n      left: 50%;\n      transform: translate(-50%, -50%);\n    }\n\n  }\n\n  &.op-autohide {\n    cursor: none;\n\n    .op-bottom-panel {\n\n      -webkit-animation-name: op-slideOutDown;\n      animation-name: op-slideOutDown;\n\n      -webkit-animation-duration: 0.2s;\n      animation-duration: 0.2s;\n      -webkit-animation-fill-mode: both;\n      animation-fill-mode: both;\n    }\n\n    .op-progressbar-container,\n    .op-controls .op-button {\n      cursor: none;\n    }\n\n    .op-caption-text-container {\n      bottom: 25px;\n    }\n  }\n\n  &.op-no-controls {\n\n    .op-controls-container {\n      display: none !important;\n    }\n  }\n\n  .op-ratio {\n    padding-bottom: 56.25%;\n    /*16:9*/\n  }\n\n  & ::-webkit-scrollbar {\n    width: 5px;\n    height: 8px;\n  }\n\n  & ::-webkit-scrollbar-button:start:decrement,\n  ::-webkit-scrollbar-button:end:increment {\n    display: block;\n    height: 3px;\n    background: #2f2f3e;\n  }\n\n  & ::-webkit-scrollbar-track {\n    background: #2f2f3e;\n    -webkit-border-radius: 10px;\n    border-radius: 10px;\n  }\n\n  & ::-webkit-scrollbar-thumb {\n    height: 50px;\n    width: 50px;\n    background: #606071;\n    -webkit-border-radius: 8px;\n    border-radius: 8px;\n  }\n\n  .op-clear {\n    color: inherit;\n    background-color: transparent;\n    padding: 0;\n    margin: 0;\n    float: none;\n    font-family: Helvetica, Arial, sans-serif;\n    font-size: 1em;\n    line-height: 1em;\n    list-style: none;\n    text-transform: none;\n    vertical-align: baseline;\n    border: 0;\n    font-variant: inherit;\n    font-stretch: inherit;\n    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);\n  }\n}\n\n.op-player {\n  position: absolute;\n  top: 0;\n  height: 100%;\n  width: 100%;\n\n\n\n  .op-core-ui-wrapper {\n    position: relative;\n    height: 100%;\n  }\n\n  .op-media-element-container {\n    /* display: block;\n    height: 100%;\n    width: auto;\n    position : relative;