n      }\n    }\n  }\n\n}\n\n/**\n * @brief   common components\n * */\n\n.op-button {\n  display: inline-block;\n  border: none;\n  background: transparent;\n  padding: 0;\n  color: inherit;\n  text-align: inherit;\n  overflow: hidden;\n  font-weight: 100;\n  text-indent: 0 !important;\n\n  &:focus,\n  & {\n    outline: 0;\n  }\n}\n\ni.op-con {\n  font-family: \"op-icons\", \"op-seek-icons\";\n  font-style: normal;\n  font-weight: normal;\n  speak: none;\n\n  font-size: inherit;\n  width: auto;\n  font-size: 30px;\n  line-height: 30px;\n\n  display: block;\n  text-decoration: inherit;\n  text-align: center;\n\n  /* For safety - reset parent styles, that can break glyph codes*/\n  font-variant: normal;\n  text-transform: none;\n\n  /* Font smoothing. That was taken from TWBS */\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n\n  cursor: pointer;\n\n  &.disable {\n    color: #4f4f4f;\n  }\n\n  &.op-close-icon {\n    &::before {\n      content: \"\\e900\";\n    }\n  }\n\n  &.op-pause-big {\n    &::before {\n      content: \"\\e911\";\n    }\n  }\n\n  &.op-fullscreen-compress {\n    &::before {\n      content: \"\\e901\";\n    }\n  }\n\n  &.op-fullscreen-expand {\n    &::before {\n      content: \"\\e902\";\n    }\n  }\n\n  &.op-arrow-left {\n    &::before {\n      content: \"\\e903\";\n    }\n  }\n\n  &.op-arrow-right {\n    &::before {\n      content: \"\\e909\";\n    }\n  }\n\n  &.op-empty-video {\n    &::before {\n      content: \"\\e904\";\n    }\n  }\n\n  &.op-play {\n    &::before {\n      content: \"\\e906\";\n    }\n  }\n\n  &.op-replay {\n    &::before {\n      content: \"\\e908\";\n    }\n  }\n\n  &.op-seek-back {\n    &::before {\n      content: \"\\e920\";\n    }\n  }\n\n  &.op-seek-forward {\n    &::before {\n      content: \"\\e921\";\n    }\n  }\n\n  &.op-playlist-icon {\n    &::before {\n      content: \"\\e907\";\n    }\n  }\n\n  &.op-replay-big {\n    &::before {\n      content: \"\\e908\";\n    }\n  }\n\n  &.op-setting {\n    &::before {\n      content: \"\\e90A\";\n    }\n  }\n\n  &.op-pause {\n    &::before {\n      content: \"\\e90C\";\n    }\n  }\n\n  &.op-volume-small {\n    display: none;\n    margin-top: -1px;\n\n    &::before {\n      content: \"\\e90D\";\n    }\n  }\n\n  &.op-volume-mute {\n    display: none;\n\n    &::before {\n      content: \"\\e90E\";\n    }\n  }\n\n  &.op-volume-max {\n    &::before {\n      content: \"\\e90F\";\n    }\n  }\n\n  &.op-play-big {\n    &::before {\n      content: \"\\e910\";\n    }\n  }\n\n  &.op-warning {\n    &::before {\n      content: \"\\e912\";\n    }\n  }\n}\n\n.op-badge {\n  display: inline-block;\n  padding: .75em .714em .714em .680em;\n  font-size: 1em;\n  line-height: 1;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: baseline;\n  border-radius: 0.286em;\n  background-color: #12121c;\n}\n\n.op-playlist {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  left: 0;\n  top: 0;\n  padding: 2.857em;\n  background: #000;\n  z-index: 6;\n\n  .op-badge {\n    position: absolute;\n    top: 0.857em;\n    right: 0.857em;\n    opacity: 0.7;\n  }\n\n  .btn-left {\n    float: left;\n    font-size: 2.857em;\n  }\n\n  .btn-right {\n    float: right;\n    font-size: 2.857em;\n  }\n\n  .op-playlist-header {\n    font-size: 2.857em;\n    font-weight: bold;\n\n    &:after {\n      display: block;\n      content: '';\n      clear: both;\n    }\n\n    .op-con.op-close-icon {\n      float: right;\n    }\n  }\n\n  .op-playlist-body {\n    height: 100%;\n    overflow: hidden;\n    position: relative;\n\n    .op-playlist-body-arrows {\n      margin-top: -15px;\n      position: absolute;\n      top: 50%;\n      height: 30px;\n      -ms-transform: translateY(-50%);\n      transform: translateY(-50%);\n      width: 100%;\n      left: 0;\n    }\n\n    .op-playlist-body-container {\n      width: 100%;\n      height: 100%;\n      position: relative;\n      margin-right: auto;\n      margin-left: auto;\n      max-width: 992px;\n\n      .op-playlist-body-center {\n        margin: 0;\n        position: absolute;\n        top: 50%;\n        padding-right: 3em;\n        padding-left: 3em;\n        -ms-transform: translateY(-50%);\n        transform: translateY(-50%);\n        width: 100%;\n      }\n    }\n\n    .op-playlist-body-row {\n      margin-right: -15px;\n      margin-left: -15px;\n      display: -ms-flexbox;\n      display: flex;\n      -ms-flex-wrap: wrap;\n      flex-wrap: wrap;\n\n\n      .op-playlist-card {\n        padding-right: 15px;\n        padding-left: 15px;\n        margin: 15px 0;\n        -ms-flex-preferred-size: 0;\n        flex-basis: 0;\n        -ms-flex-positive: 1;\n        flex-grow: 1;\n        -ms-flex: 0 0 33.333333%;\n        flex: 0 0 33.333333%;\n        max-width: 33.333333%;\n        cursor: pointer;\n\n        &.active {\n          color: #50e3c2;\n          color: var(--op-accent-color);\n\n          .op-playlist-card-thumbnail {\n            border-color: #50e3c2;\n            border-color: var(--op-accent-color);\n          }\n        }\n\n        .op-playlist-card-thumbnail {\n          position: relative;\n          display: block;\n          width: 100%;\n          padding: 0;\n          overflow: hidden;\n          border: 0.214em solid transparent;\n          background-color: #000;\n\n          img {\n            position: absolute;\n            top: 0;\n            bottom: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n            border: 0;\n          }\n\n          &.empty {\n            background-color: #bababa;\n\n            &>i {\n              margin: 0;\n              position: absolute;\n              top: 50%;\n              left: 50%;\n              transform: translate(-50%, -50%);\n              color: #fff !important;\n              font-size: 1.6em !important;\n            }\n          }\n\n          &::before {\n            padding-top: 56.25%;\n            display: block;\n            content: \"\";\n          }\n        }\n\n        .op-playlist-card-title {\n          margin-top: 0.857em;\n          font-size: 1.429em;\n          padding: 2px 0;\n          white-space: nowrap;\n          overflow: hidden;\n          text-overflow: ellipsis;\n        }\n\n      }\n    }\n\n  }\n}\n\n\n/**\n * @brief   Message Box\n * */\n\n.op-message-box {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: 4;\n\n  .op-message-container {\n    position: absolute;\n    top: 45%;\n    margin-top: -35px;\n    width: 100%;\n    padding: 0 12px;\n    text-align: center;\n\n    .op-message-text {\n      display: inline-block;\n      background-color: rgba(18, 18, 28, .3);\n      padding: 0.8em 1.2em;\n      word-wrap: break-word;\n      max-width: 80%;\n      border-radius: 4px;\n      cursor: pointer;\n\n      .op-message-description {\n        font-size: 12px;\n        margin-top: 12px;\n        color: #999;\n      }\n    }\n\n\n    .op-message-icon {\n      display: inline-block;\n      margin-top: 12px;\n      width: 100%;\n\n      i.op-con {\n        cursor: pointer;\n        font-size: 80px;\n        width: 80px;\n        height: 80px;\n        line-height: 80px;\n        display: inline-block;\n        text-shadow: 2px 2px 7px rgb(0 0 0 / 80%), 0px 0px 1px rgb(0 0 0);\n      }\n    }\n\n    /*.op-message-button{\n      display: inline-block;\n      padding : 1em;\n      margin-left : 0.4em;\n      background-color: rgba(28,28,28,.4);\n      cursor : pointer;\n    }*/\n  }\n}\n\n.op-message-box.op-message-box-default-cursor .op-message-container .op-message-text {\n  cursor: default;\n}\n\n.op-message-box.op-message-box-default-cursor .op-message-container .op-message-icon i.op-con {\n  cursor: default;\n}\n\n/**\n * @brief   Big button\n * */\n\n.op-bigbutton-container {\n  cursor: pointer;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  text-align: center;\n  z-index: 5;\n\n  .op-bigbutton {\n    width: 80px;\n    height: 80px;\n    display: block;\n    font-size: 80px;\n    line-height: 80px;\n    text-shadow: 2px 2px 7px rgb(0 0 0 / 80%), 0px 0px 1px rgb(0 0 0);\n  }\n}\n\n/**\n * @brief   Thumbnail\n * */\n\n.op-thumbnail-container {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  //padding-bottom: 56.25%;\n  top: 0;\n  left: 0;\n  //transform: translate(-50%, -50%);\n  z-index: 2;\n  pointer-events: none;\n\n  .op-thumbnail-wrapper {\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n\n    img {\n      width: 100%;\n      height: 100%;\n    }\n\n    .op-thumbnail-header {\n      position: absolute;\n      left: 1rem;\n      top: 1rem;\n      padding: 0 0.5rem;\n      font-size: 2.857em;\n      font-weight: bold;\n      line-height: 1.4;\n      text-shadow: 2px 2px 7px rgb(0 0 0 / 80%), 0px 0px 1px rgb(0 0 0);\n\n      &:after {\n        display: block;\n        content: '';\n        clear: both;\n      }\n    }\n  }\n}\n\n/**\n * @brief   WaterMark\n * */\n\n.op-watermark-container {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  //padding-bottom: 56.25%;\n  top: 0;\n  left: 0;\n  //transform: translate(-50%, -50%);\n  z-index: 3;\n\n  .op-watermark {\n    position: absolute;\n    display: inline-block;\n\n    img {\n      width: 100%;\n      height: auto;\n    }\n\n    .op-watermark-text {\n      font-size: 14px;\n    }\n  }\n}\n\n/**\n * @brief   Setting panel\n * */\n\n.op-setting-panel {\n  position: absolute;\n  bottom: 55px;\n  right: 12px;\n  overflow-y: auto;\n  max-height: 100%;\n  width: 260px;\n  //font-size: 0.8em;\n  user-select: none;\n  background-color: rgba(28, 28, 28, .9);\n  text-shadow: 0 0 2px rgba(0, 0, 0, .5);\n  z-index: 8;\n\n  &.background {\n    //opacity: 0;\n    //background-color: rgba(28,28,28,.0);\n    display: none;\n  }\n\n  .op-setting-title,\n  .op-setting-item {\n    width: 100%;\n    height: 38px;\n    line-height: 38px;\n    cursor: pointer;\n    outline: none;\n    text-align: left;\n  }\n\n  .op-setting-title-container {\n    .op-setting-title {\n      .op-setting-title-title {\n        padding-left: 12px;\n        font-weight: bold;\n        //font-size: 0.9rem;\n      }\n\n      .op-setting-title-previcon {\n        padding: 0 0 0 12px;\n        margin-right: -6px;\n      }\n\n    }\n  }\n\n  .op-setting-item-container {\n    .op-setting-item {\n      &:hover {\n        background-color: rgba(255, 255, 255, .1);\n      }\n\n      .op-setting-item-title {\n        padding-left: 12px;\n      }\n\n      .op-setting-item-nexticon {\n        float: right;\n        padding-right: 12px;\n        margin-left: -6px;\n      }\n\n      span.op-setting-item-value {\n        float: right;\n        padding-right: 12px;\n      }\n\n      .op-setting-item-checked {\n        padding-left: 12px;\n        visibility: hidden;\n      }\n\n      .op-setting-item-checked.op-show {\n        visibility: visible;\n      }\n    }\n  }\n}\n\n\n\n/**\n * @brief   Controls (container)\n * */\n\n.op-controls-container {\n  display: none;\n\n  .op-bottom-panel {\n    position: absolute;\n    left: 0px;\n    bottom: 0px;\n    width: 100%;\n    z-index: 5;\n\n    .op-gradient-bottom {\n      position: absolute;\n      width: 100%;\n      height: 100%;\n      background-color: rgba(18, 18, 18, 0.5);\n      pointer-events: none;\n      /*-moz-transition: opacity .25s cubic-bezier(0.0,0.0,0.2,1);\n      -webkit-transition: opacity .25s cubic-bezier(0.0,0.0,0.2,1);\n      transition: opacity .25s cubic-bezier(0.0,0.0,0.2,1);*/\n    }\n\n    -webkit-animation-name: op-slideInUp;\n    animation-name: op-slideInUp;\n\n    -webkit-animation-duration: 0.5s;\n    animation-duration: 0.5s;\n    -webkit-animation-fill-mode: both;\n    animation-fill-mode: both;\n\n    .op-progressbar-container {\n      display: block;\n      position: relative;\n      width: 100%;\n      height: 4px;\n      bottom: 50px;\n\n      &.op-progressbar-container-disabled {\n        cursor: default;\n      }\n\n      .op-progressbar-padding {\n        position: absolute;\n        width: 100%;\n        height: 15px;\n        bottom: -5px;\n        cursor: pointer;\n      }\n    }\n\n\n    /*contols container*/\n    .op-controls {\n      position: relative;\n      width: 100%;\n      height: 50px;\n      text-align: left;\n      overflow: hidden;\n\n      &:after {\n        content: '';\n        clear: both;\n      }\n\n      .op-setting-button {\n        position: relative;\n        margin-right: 12px;\n      }\n\n      .op-playlist-button {\n        position: relative;\n        margin-right: 12px;\n      }\n\n      .op-navigators {\n        float: left;\n        height: 30px;\n        line-height: 30px;\n      }\n\n      .op-left-controls {\n        position: absolute;\n        top: 0;\n        left: 0;\n        padding: 14px 0 10px 0;\n\n        &:after {\n          content: '';\n          clear: both;\n        }\n      }\n\n      .op-right-controls {\n        position: absolute;\n        top: 0;\n        right: 0;\n        padding: 14px 0 10px 0;\n\n        &:after {\n          content: '';\n          clear: both;\n        }\n      }\n\n      /*maybe not use*/\n      .op-frame-buttons {\n        position: relative;\n        display: inline-block;\n        margin-left: 14px;\n        overflow: hidden;\n        font-weight: 100;\n        height: 30px;\n\n        .op-frame-button {\n          margin-right: 6px;\n          position: relative;\n          text-align: center;\n          color: #fff;\n\n          .frame-icon {\n            position: relative;\n\n            &.reverse {\n              &:after {\n                content: '\\e900';\n              }\n            }\n\n            &:after {\n              font-family: 'frameIcon' !important;\n              speak: none;\n              content: '\\e901';\n              font-style: normal;\n              font-weight: normal;\n              font-variant: normal;\n              text-transform: none;\n\n              /* Better Font Rendering =========== */\n              -webkit-font-smoothing: antialiased;\n              -moz-osx-font-smoothing: grayscale;\n              position: absolute;\n              font-size: 2.4em;\n              left: 0;\n              line-height: 30px;\n              height: 30px;\n            }\n\n            .btn-text {\n              font-weight: bold;\n              font-size: 0.8em;\n              line-height: 30px;\n              height: 30px;\n            }\n\n          }\n\n        }\n\n      }\n    }\n  }\n\n}\n\n\n/**\n * @brief   Progressbar\n * */\n\n.op-progressbar {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  outline: none;\n  margin-top: 10px;\n\n  .op-play-background-color {\n    background-color: #50e3c2; // for ie 11\n    background-color: var(--op-accent-color);\n  }\n\n  .op-progress-list {\n    cursor: pointer;\n    position: relative;\n    height: 100%;\n    background: rgba(255, 255, 255, 0.2);\n\n    .op-load-progress,\n    .op-play-progress,\n    .op-hover-progress {\n      position: absolute;\n      left: 0;\n      bottom: 0;\n      width: 100%;\n      height: 100%;\n    }\n\n    .op-play-progress {\n      width: 0;\n    }\n\n    .op-load-progress {\n      width: 0;\n      background-color: rgba(255, 255, 255, .5);\n    }\n\n    .op-hover-progress {\n      left: 0;\n      width: 0;\n      background-color: rgba(255, 255, 255, .6);\n    }\n\n  }\n\n  .op-progressbar-knob-container {\n    position: absolute;\n    top: -5px;\n    left: 0;\n\n    .op-progressbar-knob {\n      width: 14px;\n      height: 14px;\n      border-radius: 7px;\n    }\n  }\n\n  .op-progressbar-time {\n    display: none;\n    position: absolute;\n    bottom: 15px;\n    left: auto;\n    width: auto;\n    background-color: rgba(28, 28, 28, 0.9);\n    border-radius: 2px;\n    padding: 5px 9px;\n    font-size: 0.8em;\n    line-height: 15px;\n    user-select: none;\n    white-space: nowrap;\n    opacity: 0.7;\n  }\n\n\n}\n\n.op-progressbar-hover .op-progressbar-time {\n  display: inline-block;\n}\n\n.op-on-error .op-progressbar-time {\n  display: none;\n}\n\n.op-progressbar-section-start,\n.op-progressbar-section-end {\n  display: none;\n  position: absolute;\n  width: 3px;\n  height: 14px;\n  bottom: -5px;\n  background-color: #50e3c2;\n  background-color: var(--op-accent-color);\n}\n\n.op-progressbar-preview {\n  position: absolute;\n  display: none;\n  bottom: 50px;\n  border: 2px solid #fff;\n  border-radius: 2px;\n  background-color: #000;\n  z-index: 9;\n}\n\n.op-play-controller {\n  margin-left: 15px;\n}\n\n/**\n * @brief   Seek button\n * */\n\n.op-seek-button {\n  //display: none;\n  position: relative;\n  top: 0px;\n\n  &.op-seek-button-back {\n    margin-left: 12px;\n  }\n\n  &.op-seek-button-forward {\n    margin-left: 6px;\n  }\n\n  i {\n    padding-top: 1px;\n    font-size: 26px;\n  }\n\n  span {\n    position: absolute;\n    top: 10.5px;\n    left: 0;\n    right: 0;\n    text-align: center;\n    font-size: 10px;\n    line-height: 10px;\n    cursor: pointer;\n  }\n}\n\n\n/**\n * @brief   Volume button\n * */\n.op-volume-controller {\n  display: inline-block;\n  position: relative;\n  margin-left: 12px;\n\n  &:after {\n    content: '';\n    clear: both;\n  }\n\n  .op-volume-button {\n    float: left;\n  }\n\n  @-webkit-keyframes slide {\n    100% {\n      left: 0;\n    }\n  }\n\n  @keyframes slide {\n    100% {\n      left: 0;\n    }\n  }\n\n  .op-volume-slider-container {\n    /*display: inline-block;*/\n    float: left;\n    opacity: 0;\n    position: relative;\n    width: 0px;\n    margin-right: 0;\n    line-height: 30px;\n    height: 30px;\n    cursor: pointer;\n    user-select: none;\n    outline: none;\n\n    &.active {\n      width: 70px;\n      opacity: 1;\n      margin-left: 8px;\n      -moz-transition: opacity .4s cubic-bezier(0.0, 0.0, 0.2, 1);\n      -webkit-transition: opacity .4s cubic-bezier(0.0, 0.0, 0.2, 1);\n      transition: opacity .4s cubic-bezier(0.0, 0.0, 0.2, 1);\n    }\n\n    .op-volume-silder {\n      height: 100%;\n      position: relative;\n\n      .op-volume-slider-bg,\n      .op-volume-slider-value {\n        position: absolute;\n        display: block;\n        left: 0;\n        top: 50%;\n        height: 4px;\n        margin-top: -2px;\n        border-radius: 10px;\n      }\n\n      .op-volume-slider-bg {\n        width: 100%;\n        background: #fff;\n      }\n\n      .op-volume-slider-value {\n        width: 100%;\n        background: #50e3c2;\n        background: var(--op-accent-color);\n        border-radius: 10px 0 0 10px;\n        //-moz-transition: width .2s cubic-bezier(0.0,0.0,0.2,1);\n        //-webkit-transition: width .2s cubic-bezier(0.0,0.0,0.2,1);\n        //transition: width .2s cubic-bezier(0.0,0.0,0.2,1);\n      }\n\n      .op-volume-slider-handle {\n        position: absolute;\n        top: 50%;\n        left: 30px;\n        width: 12px;\n        height: 12px;\n        border-radius: 10px;\n        margin-top: -6px;\n        background: #fff;\n        //-moz-transition: left .2s cubic-bezier(0.0,0.0,0.2,1);\n        //-webkit-transition: left .2s cubic-bezier(0.0,0.0,0.2,1);\n        //transition: left .2s cubic-bezier(0.0,0.0,0.2,1);\n      }\n    }\n  }\n}\n\n\n/**\n * @brief   Time Display\n * */\n\n.op-time-display {\n  float: left;\n  position: relative;\n  margin-left: 14px;\n  height: 30px;\n  line-height: 30px;\n  white-space: nowrap;\n  vertical-align: top;\n  font-size: 14px;\n  user-select: none;\n  /*흠 이부분 어떻게 처리하지*/\n\n  .op-time-current,\n  .op-time-separator,\n  .op-time-duration {}\n\n  .op-live-badge {\n    opacity: 1;\n    width: auto;\n    display: inline-block;\n\n    &:before {\n      background: #ff0000;\n      display: inline-block;\n      position: relative;\n      top: -2px;\n      width: 6px;\n      height: 6px;\n      margin-right: 5px;\n      content: '';\n      border-radius: 6px;\n    }\n\n    &.op-live-badge-delayed:before {\n      background: #fff;\n    }\n\n    .op-live-badge-lowlatency {\n      display: inline-block;\n      margin-right: 5px;\n    }\n\n    .op-live-text {\n      cursor: pointer;\n    }\n  }\n\n}\n\n\n/**\n * @brief   Context Panel\n * */\n\n.op-context-panel {\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box;\n  display: block;\n  position: absolute;\n  overflow: hidden;\n  width: 200px;\n  padding: 6px 0;\n  z-index: 8;\n  background: rgba(28, 28, 28, 0.9);\n  text-shadow: 0 0 2px rgba(0, 0, 0, .5);\n  font-weight: lighter;\n  user-select: none;\n\n  &:before,\n  &:after {\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n  }\n\n  & *,\n  & *:before,\n  & *:after {\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n  }\n\n  .op-context-item {\n    width: 100%;\n    height: 38px;\n    padding-left: 12px;\n    line-height: 38px;\n    cursor: pointer;\n    outline: none;\n    font-size: 0.8em;\n    font-weight: lighter;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    overflow: hidden;\n\n    &:hover {\n      background-color: rgba(255, 255, 255, .1);\n    }\n  }\n\n}\n\n\n\n\n\n\n/**\n * @brief   FullScreen button\n * */\n\n.op-fullscreen-button {\n  position: relative;\n  margin-right: 15px;\n\n  .op-fullscreen-compress {\n    display: none;\n  }\n}\n\n/**\n * @brief   spinner\n * */\n\n.op-spinner-container {\n  position: absolute;\n  top: 50%;\n  width: 64px;\n  left: 50%;\n  margin-left: -32px;\n  margin-top: -32px;\n  z-index: 7;\n  display: none;\n\n\n  .op-spinner {\n    display: inline-block;\n    position: relative;\n    width: 64px;\n    height: 64px;\n\n    border: 4px solid transparent;\n    border-top: 4px solid #50e3c2;\n    border-top: 4px solid var(--op-accent-color);\n    border-radius: 50%;\n\n    animation: spin 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;\n\n    @keyframes spin {\n     